import styled from 'styled-components';
import React, {useState} from 'react'
import Modal from '../../../common/Modal';
import useReadingSessionStore from "./useReadingSessionStore";
import {Button, Form} from "react-bootstrap";
import RowSpacer from "../../../common/styles/RowSpacer";
import {BooksitoutServer} from "../../../config/BooksitoutServer";
import toast from 'react-hot-toast';
import {useNavigate} from "react-router-dom";

const BookReadingSessionEndModal = () => {
    const {
        isEndModalOpen, closeEndModal,
        timerInSeconds,
    } = useReadingSessionStore()

    return (
        <Modal
            titleText={null}
            isShowing={isEndModalOpen}
            onClose={closeEndModal}
            body={<Body/>}
            size={'md'}
        />
    )
}

const Body = () => {
    const navigate = useNavigate()

    const {
        resetTimer,
        closeEndModal,
        bookId, readingSessionId, timerInSeconds
    } = useReadingSessionStore()

    const [page, setPage] = useState<string | null>()

    const handleSave = (e) => {
        e.preventDefault()

        const body = {
            endTime: new Date(),
            readingTimeInMinutes: timerInSeconds / 60,
            startPage: null,
            endPage: page,
        }

        BooksitoutServer
            .post(`/v1/book/reading-session/${readingSessionId}/end`, body)
            .then((res) => {
                toast.success(' ')
                resetTimer()
                navigate(`/book/mine/${bookId}`)
            })
            .catch((err) => toast.error(' .     '))
    }

    return (
        <Form onSubmit={handleSave}>
            <Title>   </Title>
            <Form.Control type="number" placeholder={' '} onChange={(e) => setPage(e.target.value)}/>
            <RowSpacer/>

            <ButtonsContainer>
                <ButtonContainer>
                    <Button variant={'book'} className={'w-100'}>  </Button>
                </ButtonContainer>

                <ButtonContainer>
                    <Button variant={'book'} className={'w-100'} type={'submit'}>{page == null || page == '' ? '  ' : ''}</Button>
                </ButtonContainer>
            </ButtonsContainer>

            <RowSpacer size={10}/>

            <CancelButtonContainer>
                <CancelButton onClick={closeEndModal}></CancelButton>
            </CancelButtonContainer>
        </Form>
    )
}

const Title = styled.h1`
    font-size: 1rem;
    font-weight: bold;
    margin-bottom: 1rem;
    text-align: center;
`

const ButtonsContainer = styled.div.attrs({
    className: 'row'
})`
`

const ButtonContainer = styled.div.attrs({
    className: 'col-6'
})`
`

const CancelButtonContainer = styled.div`
    display: flex;
    justify-content: center;
`

const CancelButton = styled(Button).attrs({
    variant: 'book-danger'
})`
    width: 75%;
`

export default BookReadingSessionEndModal
